import React from "react";
import { BrowserRouter as Router, Route, Routes, Navigate } from "react-router-dom";
import NotFoundPage from "./pages/Main Pages/NotFoundPage";
import Login from "./pages/Main Pages/Login";
import { userinfo } from "./Api/Auth.js";
import Home from "./pages/Main Pages/Home";
import Element from "./pages/Admin/HowTarget/Element.js";
import Criteria from "./pages/Admin/HowTarget/Criteria.js";
import Behavior from "./pages/Admin/HowTarget/Behavior.js";
import SkillLevel from "./pages/Admin/HowTarget/SkillLevel.js";
import Users from "./pages/Admin/Users.js";
import UserSkills from "./pages/Main Pages/UserSkills.js";
import EmployeesSkills from "./pages/Manager/EmployeesSkills.js";
function App() {
  const userinfomation = userinfo(localStorage.getItem('access_token'));
  return (
    <Router>
      <div>
        <Routes>
          <Route path="/" element= {userinfomation ? <Home user = {userinfomation} /> : <Navigate to="/login" />} />
          <Route path="/login" element={<Login />} />
          <Route path="/not-found" element={<NotFoundPage />}/>
          <Route path="/skills/element" element= {userinfomation ? userinfomation.role === 'Admin'  ? <Element user = {userinfomation} /> : <Navigate to="/not-found" /> : <Navigate to="/login" />} />
          <Route path="/skills/criteria" element= {userinfomation ? userinfomation.role === 'Admin'  ? <Criteria user = {userinfomation} /> : <Navigate to="/not-found" /> : <Navigate to="/login" />} />
          <Route path="/skills/behavior" element= {userinfomation ? userinfomation.role === 'Admin' ? <Behavior user = {userinfomation} /> : <Navigate to="/not-found" /> : <Navigate to="/login" />} />
          <Route path="/skills/skill-level" element= {userinfomation ? userinfomation.role === 'Admin' ? <SkillLevel user = {userinfomation} /> : <Navigate to="/not-found" /> : <Navigate to="/login" />} />
          <Route path="/users" element= {userinfomation ? userinfomation.role === 'Admin' ? <Users user = {userinfomation} /> : <Navigate to="/not-found" /> : <Navigate to="/login" />} />
          <Route path="/manager/employees-skills" element= {userinfomation ? userinfomation.role === 'Manager' ? <EmployeesSkills user = {userinfomation} /> : <Navigate to="/not-found" /> : <Navigate to="/login" />} />
          <Route path="/skills/user-skill/:id" element= {userinfomation ?  <UserSkills user = {userinfomation} />  : <Navigate to="/login" />} />
          <Route path="*" element={<Navigate to="/not-found" />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;

