import React, { useState, useEffect, useMemo } from 'react';
import Navbar from '../../../components/Navbar';
import { Container, Row, Col, Button, Table, Pagination, Form, Modal, Breadcrumb } from 'react-bootstrap';
import Swal from 'sweetalert2';
import { getCriteria, addCriteria, updateCriteria, deleteCriteria } from '../../../Api/Criteria';
import { getElements } from '../../../Api/Element';
import Success from '../../../components/Sucees';
import Error from '../../../components/Erorr';
import Warning from '../../../components/Warning';

import { Spinner } from 'react-bootstrap';
function Criteria(props) {
    const [criteriaList, setCriteriaList] = useState([]); 
    const [elementList, setElementList] = useState([]);   
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 8;
    const [filter, setFilter] = useState({ field: '', query: '', orderBy: '' });
    const [showModal, setShowModal] = useState(false);
    const [modalData, setModalData] = useState(null);

    const [criteriaName, setCriteriaName] = useState('');
    const [criteriaDescription, setCriteriaDescription] = useState('');
    const [criteriaElementname, setCriteriaElementname] = useState('');

    const [loading, setLoading] = useState(false);

    useEffect(() => {
        document.title ="Criterias - Siemens Employee Management System";

        const fetchData = async () => {
            setLoading(true);
            try {
            const criteriaResponse = await getCriteria();
            setCriteriaList(criteriaResponse);
            const elementResponse = await getElements();
            setElementList(elementResponse);
        } 
        catch (error) {
            Error('An error occurred while fetching criteria');
        } 
        finally {
            setLoading(false);
        }
    };
    fetchData();
    }, []);  

    useEffect(() => {
        if (modalData) {
            setCriteriaName(modalData.name);
            setCriteriaDescription(modalData.description);
            setCriteriaElementname(modalData.element);
        } else {
            setCriteriaName('');
            setCriteriaDescription('');
            setCriteriaElementname('');
        }
    }, [modalData]);

    const filteredCriteria = useMemo(() => {
        const { field, query } = filter;
        if (!field) return criteriaList;

        return criteriaList.filter(criteria => {
            let value = criteria[field]; 
            if (field === 'created_at') {
                value = new Date(criteria[field]).toLocaleString();
            } else if (field === 'element_name') {
                value = criteria.element ;
            }
            if (value === undefined || value === null) return false;
            return value.toString().toLowerCase().includes(query.toLowerCase()); 
        });
    }, [criteriaList, filter, elementList]);

    const sortedCriteria = useMemo(() => {
        if (!filter.orderBy) return filteredCriteria;
        return [...filteredCriteria].sort((a, b) => {
            const field = filter.orderBy;
            let valA = a[field];
            let valB = b[field];

            if (field === 'created_at') {
                valA = new Date(a[field]);
                valB = new Date(b[field]);
            } else if (field === 'element_name') {
                valA = a.element;
                valB = b.element;
            }

            if (typeof valA === 'string') {
                return valA.localeCompare(valB); 
            } else if (typeof valA === 'number' || valA instanceof Date) {
                return valA - valB;
            }

            return 0;
        });
    }, [filteredCriteria, filter.orderBy, elementList]);

    const currentCriteria = useMemo(() => {
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        return sortedCriteria.slice(indexOfFirstItem, indexOfLastItem);
    }, [sortedCriteria, currentPage, itemsPerPage]);

    const totalPages = Math.ceil(sortedCriteria.length / itemsPerPage);

    useEffect(() => {
        const newTotalPages = Math.ceil(sortedCriteria.length / itemsPerPage);
        if (currentPage > newTotalPages) {
            setCurrentPage(newTotalPages > 0 ? newTotalPages : 1);
        }
    }, [sortedCriteria.length, itemsPerPage, currentPage]);

    const handleDelete = async (id) => {
        if (!id) {
            Error('Criteria Not Found ! Please Refresh the Page');
            return;
        }
        Warning('Warning', 'Are you sure you want to delete this element?', () => {
            deleteCriteria(id).then((response) => {
                const updatedList = criteriaList.filter(criteria => criteria.id !== id);
                setCriteriaList(updatedList);
                Success(response);
            }).catch((error) => {
                Error(error.response.data); 
            });
        }
        );

    };
    const handleAdd = () => {
        setModalData(null); 
        setShowModal(true);
    };

    const handleEdit = (criteria) => {
        setModalData(criteria);
        setShowModal(true);
    };

    const handleFormSubmit = async () => {
        let  formData = {
            name: criteriaName,
            description: criteriaDescription,
            created_at: new Date().toISOString(),
            element: (elementList.find(el => el.name === criteriaElementname)).id,
        };
        if (modalData) {
            if (!modalData.id) {
                Error('Criteria Not Found ! Please Refresh the Page');
                return;
            }
            else if (criteriaList.find(criteria => criteria.name === formData.name && criteria.id !== modalData.id)) {
                Error('Criteria with this name already exists');
                return;
            }
            updateCriteria(modalData.id, formData).then((response) => {
                let updatedCriteria = response.new_data;
                setCriteriaList(criteriaList.map(criteria => criteria.id === updatedCriteria.id ? updatedCriteria : criteria));
                Success(response.message);
            }
            ).catch((error) => {
                Error(error.response.data);
            });

        } else {
            if (criteriaList.find(criteria => criteria.name === formData.name )) {  
                Error('Criteria with this name already exists');
                return;
            }
            addCriteria(formData).then((response) => {
                let newCriteria = response.new_data;
                setCriteriaList([...criteriaList, newCriteria]);
                Success(response.message);
            }).catch((error) => {
                Error(error.response.data);
            });
        }
        setCriteriaName('');
        setCriteriaDescription('');
        setCriteriaElementname('');
        setShowModal(false);
    };

    const handleSearch = (e) => {
        const { name, value } = e.target;
        setFilter(prev => ({ ...prev, [name]: value }));
        setCurrentPage(1);
    };

    return (
        <div id="pages" className="m-0 p-0">
            {loading ? (
                <div className="d-flex justify-content-center align-items-center bg-light" style={{ height: '100vh' }}>
                <Spinner animation="border" role="status" style={{ width: '50px', height: '50px', color: '#008080' }}>
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
                </div>
            ) : (
            <>
            <Navbar user={props.user} className="mb-5" />
            <Container className="mt-5">
                <Row className="mb-3 align-items-center">
                    {/* Heading */}
                    <Col lg={3} className="text-center mb-2">
                        <h3>Criteria List</h3>
                    </Col>

                    <Col lg={4} className="mb-2">
                        <div className="d-flex border border-2 rounded-3">
                            <Form.Select
                                className="border-end"
                                style={{ width: '40%' }}
                                name="field"
                                value={filter.field}
                                onChange={handleSearch}
                                aria-label="Filter by"
                            >
                                <option value="" disabled> Filter by</option>
                                <option value="name">Name</option>
                                <option value="description">Description</option>
                                <option value="created_at">Created At</option>
                                <option value="element_name">Element</option>
                            </Form.Select>

                            <div style={{ display: 'flex', alignItems: 'center', width: '60%' }} id="search">
                                <Form.Control
                                    type="text"
                                    name="query"
                                    value={filter.query}
                                    onChange={handleSearch}
                                    placeholder="Search"
                                    style={{ width: '100%' }}
                                    aria-label="Search"
                                />
                                <i
                                    className="fa fa-search"
                                    style={{
                                        color: '#6c757d',
                                        textAlign: 'end',
                                        marginLeft: '-30px',
                                    }}
                                    aria-hidden="true"
                                ></i>
                            </div>
                        </div>
                    </Col>

                    <Col lg={2} className="mb-2 d-flex">
                        <Form.Select
                            className="me-2"
                            value={filter.orderBy}
                            onChange={(e) => { setFilter(prev => ({ ...prev, orderBy: e.target.value })); setCurrentPage(1); }}
                            aria-label="Order by"
                        >
                            <option value="" disabled>Order by</option>
                            <option value="name">Name</option>
                            <option value="description">Description</option>
                            <option value="created_at">Created At</option>
                            <option value="element_name">Element</option>
                        </Form.Select>
                    </Col>

                    <Col lg={3} className="text-center">
                        <Button variant="primary" onClick={handleAdd}>
                            <i className="bi bi-plus me-2"></i> Add New Criteria
                        </Button>
                    </Col>
                </Row>

                <hr className="mt-3 text-muted" />

                <Table hover striped className="text-center mt-3">
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Description</th>
                            <th>Created At</th>
                            <th>Element</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {currentCriteria.length > 0 ? (
                            currentCriteria.map(criteria => (
                                <tr key={criteria.id}>
                                    <td>{criteria.name}</td>
                                    <td>{criteria.description}</td>
                                    <td>{new Date(criteria.created_at).toLocaleString()}</td>
                                    <td>{criteria.element}</td>
                                    <td>
                                        <i
                                            className="fa fa-pencil me-3"
                                            style={{ cursor: 'pointer' }}
                                            onClick={() => handleEdit(criteria)}
                                            aria-hidden="true"
                                        ></i>
                                        <i
                                            className="fa fa-trash"
                                            style={{ cursor: 'pointer' }}
                                            onClick={() => handleDelete(criteria.id)}
                                            aria-hidden="true"
                                        ></i>
                                    </td>
                                </tr>
                            ))
                        ) : (
                            <tr>
                                <td colSpan="5">No criteria found</td>
                            </tr>
                        )}
                    </tbody>
                </Table>

                {totalPages > 1 && (
                    <Pagination className="justify-content-center">
                        <Pagination.Prev
                            onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                            disabled={currentPage === 1}
                        >
                            Previous
                        </Pagination.Prev>
                        {Array.from({ length: totalPages }, (_, index) => (
                            <Pagination.Item
                                key={index + 1}
                                active={index + 1 === currentPage}
                                onClick={() => setCurrentPage(index + 1)}
                            >
                                {index + 1}
                            </Pagination.Item>
                        ))}
                        <Pagination.Next
                            onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                            disabled={currentPage === totalPages}
                        >
                            Next
                        </Pagination.Next>
                    </Pagination>
                )}

                <Modal show={showModal} onHide={() => setShowModal(false)} backdrop="static" keyboard={false} centered>
                    <Modal.Header closeButton>
                        <Modal.Title className='text-center w-100'>{modalData ? 'Edit Criteria' : 'Add New Criteria'}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>  
                        <Form>
                            <Form.Group className="mb-3 d-none" controlId="alert" id='alert'>
                                <Form.Text >
                                <div className="alert alert-danger text-center" role="alert">
                                    Please fill out all the fields!
                                </div>
                                </Form.Text>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="criteriaName">
                                <Form.Label>Name</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Enter name of the criteria"
                                    value={criteriaName}
                                    onChange={(e) => setCriteriaName(e.target.value)}
                                />                            
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="criteriaDescription">
                                <Form.Label>Description</Form.Label>
                                <Form.Control
                                    as="textarea"
                                    placeholder="Enter description"
                                    value={ criteriaDescription}
                                    onChange={(e) => setCriteriaDescription(e.target.value)}
                                />
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="criteriaElement">
                                <Form.Label>Element</Form.Label>
                                <Form.Select
                                    value={criteriaElementname}
                                    onChange={(e) => setCriteriaElementname(e.target.value)}
                                >
                                    <option value="" disabled>Select an Element</option>
                                
                                    {elementList.map(element => (
                                        <option  key={element.id} value={element.name} >                                           
                                            {element.name}    
                                        </option>

                                    ))}
                                </Form.Select>
                            </Form.Group>
                            <div className="d-flex justify-content-center">
                            <Button variant="success" className='justify-content-center w-25' style={{ background: '#008080' }} 
                            onClick={ () =>{
                                if (criteriaName === '' || criteriaDescription === '' || criteriaElementname === '') {
                                    document.getElementById('alert').classList.remove('d-none');
                                    setTimeout(() => {
                                        if (document.getElementById('alert')) {
                                            document.getElementById('alert').classList.add('d-none');
                                        }
                                    }, 3000);
                                    return;
                                } 
                                handleFormSubmit();
                            }       
                            }>
                                {modalData ? 'Update' : 'Add'}
                            </Button>
                            </div>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Container>
            </>
            )}
        </div>
    );
}

export default Criteria;
