import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Offcanvas from 'react-bootstrap/Offcanvas';
import '../styles/Navbar.css';
import Swal from 'sweetalert2';
import {logout} from '../Api/Auth';
import {changePassword} from '../Api/User';
import Success from './Sucees';
import Error from './Erorr';

function OffcanvasExample(props) {
  const expand = 'lg';  
  function handleLogout() {
    Swal.fire({
      title: 'Are you sure you want to logout?',
      showCancelButton: true,
      confirmButtonText: `Yes`,
      cancelButtonText: `No`,
      icon: 'warning',
      confirmButtonColor: '#009999',
      iconColor: '#009999',
      showCloseButton: true,  
    }).then((result) => {
      if (result.isConfirmed) {
        logout();
        Success('You have successfully logged out!');
        setTimeout(() => {
          window.location.href = '/login';
        }, 2000);
      }
    });
  }
  function handleChangePassword() {
    const PasswordPattern = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/;
    Swal.fire({
      title: 'Change Password',
      html: `<form id="changePasswordForm">
          <div class="form-group mb-3">
            <label for="oldPassword">Old Password</label>
            <input type="password" class="form-control" id="oldPassword" required>
          </div>
          <div class="form-group mb-3">
            <label for="newPassword">New Password</label>
            <input type="password" class="form-control" id="newPassword" required>
            <small id="passwordHelp" class="form-text text-muted">
              Password must be at least 8 characters, include one uppercase letter, one lowercase letter, and one number.
            </small>
          </div>
          <div class="form-group mb-3">
            <label for="confirmPassword">Confirm Password</label>
            <input type="password" class="form-control" id="confirmPassword" required>
          </div>
        </form>`,
      showCancelButton: true,
      confirmButtonText: `Change`,
      cancelButtonText: `Cancel`,
      confirmButtonColor: '#009999',
      showCloseButton: true,
      preConfirm: () => {
        const newPassword = document.getElementById('newPassword').value;
        const confirmPassword = document.getElementById('confirmPassword').value;
        if (newPassword === document.getElementById('oldPassword').value) {
          Swal.showValidationMessage('New password cannot be the same as the old password');
          return false;
        }
        if (!PasswordPattern.test(newPassword)) {
          Swal.showValidationMessage('Password does not match the required pattern');
          return false;
        }
  
        if (newPassword !== confirmPassword) {
          Swal.showValidationMessage('Passwords do not match');
          return false;
        }
        return true;
      },
    }).then((result) => {
      if (result.isConfirmed) {
        const oldPassword = document.getElementById('oldPassword').value;
        const newPassword = document.getElementById('newPassword').value;
        const userId = props.user.id;
          changePassword(userId, oldPassword, newPassword).then((response) => {
            Success(response);
          }).catch((error) => {
            Error(error.response.data);
          });        
      }
    });
  }
  return (
    <div className='bg-white shadow'>
      <Navbar expand={expand} className='container'>
        <Container fluid>
          <Navbar.Brand href="/" className="Logo"  > Siemens </Navbar.Brand>
          <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-${expand}`}  />
          <Navbar.Offcanvas
            id={`offcanvasNavbar-expand-${expand}`}
            aria-labelledby={`offcanvasNavbarLabel-expand-${expand}`}
            placement="end"
          >
            <Offcanvas.Header closeButton>
              <Offcanvas.Title id= {`  offcanvasNavbarLabel-expand-${expand} `} style={{color: '#009999'}}>
                Siemens Skill Management System
              </Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body>
              <Nav className="justify-content-end flex-grow-1 pe-3 fs-5 fw-medium">
                <Nav.Link href="/">Home</Nav.Link>
                {props.user.role ==="Admin" ? (
                  <>
                  <Nav.Link href="/users">Users</Nav.Link>
                <NavDropdown
                  title="How Target"
                  id={`offcanvasNavbarDropdown-expand-${expand}`}
                >
                  <NavDropdown.Item href="/skills/element">Elements</NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item href="/skills/criteria">
                    Criteria
                  </NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item href="/skills/behavior">
                    Behaviors
                  </NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item href="/skills/skill-level">
                  Behaviors Levels
                  </NavDropdown.Item>
                </NavDropdown> 
                  </>
                  
                ) : props.user.role ==="Manager" ? (
                  <>
                  <Nav.Link href="/manager/employees-skills">Users</Nav.Link>
                  <Nav.Link href={`/skills/user-skill/${props.user.id}`}>My Skills</Nav.Link>
                  </>
                ) : (
                  <>
                  <Nav.Link href={`/skills/user-skill/${props.user.id}`}> My Skills</Nav.Link>
                  </>
                )}

                <NavDropdown
                  title="Account"
                  id={`offcanvasNavbarDropdown-expand-${expand}`}
                >
                  <NavDropdown.Item    onClick={handleChangePassword}> Change Password</NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item 
                  onClick={handleLogout}>Logout</NavDropdown.Item>
                </NavDropdown>
              </Nav>
            </Offcanvas.Body>
          </Navbar.Offcanvas>
        </Container>
      </Navbar>
    </div>
  );
}

export default OffcanvasExample;
