import React, { useState, useEffect, useMemo } from 'react';
import Navbar from '../../../components/Navbar';
import { Container, Row, Col, Button, Table, Pagination, Form, Modal, Spinner } from 'react-bootstrap';
import Swal from 'sweetalert2';
import Success from '../../../components/Sucees';
import Error from '../../../components/Erorr';
import Warning from '../../../components/Warning';

import { getSkillLevels, addSkillLevel, updateSkillLevel, deleteSkillLevel } from '../../../Api/SkillLevel';
import { getBehaviors } from '../../../Api/Behavior';

function SkillLevel(props) {
    const [skillLevelList, setSkillLevelList] = useState([]);
    const [behaviorList, setBehaviorList] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 8;
    const [filter, setFilter] = useState({ field: '', query: '', orderBy: '' });
    const [showModal, setShowModal] = useState(false);
    const [modalData, setModalData] = useState(null);

    const [skillLevelNumber, setSkillLevelNumber] = useState('');
    const [skillLevelName, setSkillLevelName] = useState('');
    const [skillLevelDescription, setSkillLevelDescription] = useState('');
    const [skillLevelBehavior, setSkillLevelBehavior] = useState('');
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        const fetchData = async () => {
            document.title = 'Skill Levels - Performance Management System';
            setLoading(true);
            try {
                const skillLevelResponse = await getSkillLevels();
                setSkillLevelList(skillLevelResponse);
                const behaviorResponse = await getBehaviors();
                setBehaviorList(behaviorResponse);
            } catch (error) {
                Error('An error occurred while fetching data');
            } finally {
                setLoading(false);
            }
        };
        fetchData();
    }, []);

    useEffect(() => {
        if (modalData) {
            setSkillLevelNumber(modalData.level_number);
            setSkillLevelName(modalData.name);
            setSkillLevelDescription(modalData.description);
            setSkillLevelBehavior(modalData.behavior);
        } else {
            setSkillLevelNumber('');
            setSkillLevelName('');
            setSkillLevelDescription('');
            setSkillLevelBehavior('');
        }
    }, [modalData]);

    const filteredSkillLevels = useMemo(() => {
        const { field, query } = filter;
        if (!field) return skillLevelList;

        return skillLevelList.filter(skillLevel => {
            let value = skillLevel[field];
            if (field === 'created_at') {
                value = new Date(skillLevel[field]).toLocaleString();
            } else if (field === 'behavior_name') {
                value =skillLevel.behavior 
            }
            return value && value.toString().toLowerCase().includes(query.toLowerCase());
        });
    }, [skillLevelList, filter, behaviorList]);

    const sortedSkillLevels = useMemo(() => {
        if (!filter.orderBy) return filteredSkillLevels;

        return [...filteredSkillLevels].sort((a, b) => {
            console.log(a, b);
            const field = filter.orderBy;
            let valA = a[field];
            let valB = b[field];

            if (field === 'created_at') {
                valA = new Date(a[field]);
                valB = new Date(b[field]);
            } else if (field === 'behavior_name') {
                valA =  a.behavior;
                valB = b.behavior;
            }
            else if (field === 'level_number') {    
                valA = parseInt(a[field]);
                valB = parseInt(b[field]);
            }
            else if (field === 'name') {
                valA = a[field].toLowerCase();
                valB = b[field].toLowerCase();
            }
            else if (field === 'description') {
                valA = a[field].toLowerCase();
                valB = b[field].toLowerCase();
            }
            return typeof valA === 'string' ? valA.localeCompare(valB) : valA - valB;
        });
    }, [filteredSkillLevels, filter.orderBy, behaviorList]);

    const currentSkillLevels = useMemo(() => {
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        return sortedSkillLevels.slice(indexOfFirstItem, indexOfLastItem);
    }, [sortedSkillLevels, currentPage, itemsPerPage]);

    const totalPages = Math.ceil(sortedSkillLevels.length / itemsPerPage);

    useEffect(() => {
        const newTotalPages = Math.ceil(sortedSkillLevels.length / itemsPerPage);
        if (currentPage > newTotalPages) {
            setCurrentPage(newTotalPages > 0 ? newTotalPages : 1);
        }
    }, [sortedSkillLevels.length, itemsPerPage, currentPage]);

    const handleDelete = (id) => {
        if (!id) {
            Error('An error occurred !Please Refresh the Page');
            return;
        }
        Warning('Are you sure?', 'Do you want to delete this Skill Level?', () => {
            deleteSkillLevel(id).then((response) => {
                const updatedList = skillLevelList.filter(skillLevel => skillLevel.id !== id);
                setSkillLevelList(updatedList);
                Success(response);
            }).catch((error) => {
                Error(error.response.data);
            });
        });
    };

    const handleAdd = () => {
        setModalData(null);
        setShowModal(true);
    };

    const handleEdit = (skillLevel) => {
        setModalData(skillLevel);
        setShowModal(true);
    };

    const handleFormSubmit = () => {
        const formData = {
            level_number: skillLevelNumber,
            name: skillLevelName,
            description: skillLevelDescription,
            behavior: behaviorList.find(b => b.name === skillLevelBehavior).id,
            created_at: '',
        };
        const index = skillLevelList.findIndex(sl =>sl.level_number == skillLevelNumber && sl.behavior ==skillLevelBehavior);
        if (modalData) {
            if (skillLevelList.findIndex(sl => sl.id === modalData.id) === -1) {
                Error('Skill Level not found! Please refresh the page');
                return;
            }
            else if (index !== -1 && skillLevelList[index].id !== modalData.id) {
                Error('Level Number already exists');
                return;
            }
            updateSkillLevel(modalData.id, formData).then((response) => {
                const updatedList = skillLevelList.map(sl => (sl.id === modalData.id ? response.new_data : sl));
                setSkillLevelList(updatedList);
                Success('Skill Level updated successfully');
            }).catch((error) => {
                Error(error.response.data);
            });
        } else {
            if (index !== -1) {
                Error('Level Number already exists');
                return;
            }
            addSkillLevel(formData).then((response) => {
                setSkillLevelList([...skillLevelList, response.new_data]);
                Success('Skill Level added successfully');
            }).catch((error) => {
                Error(error.response.data);
            });
        }
        setSkillLevelNumber('');
        setSkillLevelName('');
        setSkillLevelDescription('');
        setSkillLevelBehavior('');
        setShowModal(false);
    };

    const handleSearch = (e) => {
        const { name, value } = e.target;
        setFilter(prev => ({ ...prev, [name]: value }));
        setCurrentPage(1);
    };

    return (
        <div id="pages" className="m-0 p-0">
            {loading ? (
                <div className="d-flex justify-content-center align-items-center bg-light" style={{ height: '100vh' }}>
                    <Spinner animation="border" role="status" style={{ width: '50px', height: '50px', color: '#008080' }}>
                        <span className="visually-hidden">Loading...</span>
                    </Spinner>
                </div>
            ) : (
                <>
                    <Navbar user={props.user} className="mb-5" />
                    <Container className="mt-5">
                        <Row className="mb-3 align-items-center">
                            <Col lg={3} className="text-center mb-2">
                                <h3>Skills Levels List</h3>
                            </Col>

                            <Col lg={4} className="mb-2">
                                <div className="d-flex border border-2 rounded-3">
                                    <Form.Select
                                        className="border-end"
                                        style={{ width: '40%' }}
                                        name="field"
                                        value={filter.field}
                                        onChange={handleSearch}
                                        aria-label="Filter by"
                                    >
                                        <option value="" disabled>Filter by</option>
                                        <option value="name">Name</option>
                                        <option value="level_number">Level Number</option>
                                        <option value="description">Description</option>
                                        <option value="created_at">Created At</option>
                                        <option value="behavior_name">Behavior</option>
                                    </Form.Select>

                                    <div style={{ display: 'flex', alignItems: 'center', width: '60%' }} id="search">
                                        <Form.Control
                                            type="text"
                                            name="query"
                                            value={filter.query}
                                            onChange={handleSearch}
                                            placeholder="Search"
                                            style={{ width: '100%' }}
                                            aria-label="Search"
                                        />
                                        <i className="fa fa-search" style={{ color: '#6c757d', textAlign: 'end', marginLeft: '-30px' }} aria-hidden="true"></i>
                                    </div>
                                </div>
                            </Col>

                            <Col lg={2} className="mb-2 d-flex">
                                <Form.Select
                                    className="me-2"
                                    value={filter.orderBy}
                                    onChange={(e) => { setFilter(prev => ({ ...prev, orderBy: e.target.value })); setCurrentPage(1); }}
                                    aria-label="Order by"
                                >
                                
                                <option value="" disabled>Order by</option>
                                    <option value="name">Name</option>
                                    <option value="level_number">Level Number</option>
                                    <option value="description">Description</option>
                                    <option value="created_at">Created At</option>
                                    <option value="behavior_name">Behavior</option>
                                </Form.Select>
                            </Col>

                            <Col lg={3} className="text-center">
                                <Button variant="primary" onClick={handleAdd}>
                                    <i className="bi bi-plus me-2"></i> Add New Skill Level
                                </Button>
                            </Col>
                        </Row>

                        <hr className="mt-3 text-muted" />

                        <Table hover striped className="text-center mt-3">
                            <thead>
                                <tr>
                                    <th>Level Number</th>
                                    <th>Name</th>
                                    <th>Description</th>
                                    <th>Created At</th>
                                    <th>Behavior</th>
                                    <th>Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {currentSkillLevels.length > 0 ? (
                                    currentSkillLevels.map(skillLevel => (
                                        <tr key={skillLevel.id}>
                                            <td>{skillLevel.level_number}</td>
                                            <td>{skillLevel.name}</td>
                                            <td>{skillLevel.description}</td>
                                            <td>{new Date(skillLevel.created_at).toLocaleString()}</td>
                                            <td>{skillLevel.behavior}</td>
                                            <td>
                                                <i
                                                    className="fa fa-pencil me-3"
                                                    style={{ cursor: 'pointer' }}
                                                    onClick={() => handleEdit(skillLevel)}
                                                    aria-hidden="true"
                                                ></i>
                                                <i
                                                    className="fa fa-trash"
                                                    style={{ cursor: 'pointer' }}
                                                    onClick={() => handleDelete(skillLevel.id)}
                                                    aria-hidden="true"
                                                ></i>
                                            </td>
                                        </tr>
                                    ))
                                ) : (
                                    <tr>
                                        <td colSpan="6">No skill levels found</td>
                                    </tr>
                                )}
                            </tbody>
                        </Table>

                        {totalPages > 1 && (
                            <Pagination className="justify-content-center">
                                <Pagination.Prev
                                    onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                                    disabled={currentPage === 1}
                                >
                                    Previous
                                </Pagination.Prev>
                                {Array.from({ length: totalPages }, (_, index) => (
                                    <Pagination.Item
                                        key={index + 1}
                                        active={index + 1 === currentPage}
                                        onClick={() => setCurrentPage(index + 1)}
                                    >
                                        {index + 1}
                                    </Pagination.Item>
                                ))}
                                <Pagination.Next
                                    onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                                    disabled={currentPage === totalPages}
                                >
                                    Next
                                </Pagination.Next>
                            </Pagination>
                        )}

                        <Modal show={showModal} onHide={() => setShowModal(false)} backdrop="static" keyboard={false} centered>
                            <Modal.Header closeButton>
                                <Modal.Title className='text-center w-100'>{modalData ? 'Edit Skill Level' : 'Add New Skill Level'}</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <Form>
                                    <Form.Group className="mb-3 d-none" controlId="alert" id='alert'>
                                        <Form.Text>
                                            <div className="alert alert-danger text-center" role="alert" id = 'alertmsg'>
                                            </div>
                                        </Form.Text>
                                    </Form.Group>
                                    <Form.Group className="mb-3" controlId="skillLevelNumber">
                                        <Form.Label>Level Number</Form.Label>
                                        <Form.Control
                                            type="number"
                                            placeholder="Enter level number"
                                            value={skillLevelNumber}
                                            onChange={(e) => setSkillLevelNumber(e.target.value)}
                                            min='1'
                                        />
                                    </Form.Group>
                                    <Form.Group className="mb-3" controlId="skillLevelName">
                                        <Form.Label>Name</Form.Label>
                                        <Form.Control
                                            type="text"
                                            placeholder="Enter name of the skill level"
                                            value={skillLevelName}
                                            onChange={(e) => setSkillLevelName(e.target.value)}
                                        />
                                    </Form.Group>
                                    <Form.Group className="mb-3" controlId="skillLevelDescription">
                                        <Form.Label>Description</Form.Label>
                                        <Form.Control
                                            as="textarea"
                                            placeholder="Enter description"
                                            value={skillLevelDescription}
                                            onChange={(e) => setSkillLevelDescription(e.target.value)}
                                        />
                                    </Form.Group>
                                    <Form.Group className="mb-3" controlId="skillLevelBehavior">
                                        <Form.Label>Behavior</Form.Label>
                                        <Form.Select
                                            aria-label="Select Behavior"
                                            value={skillLevelBehavior}
                                            onChange={(e) => setSkillLevelBehavior(e.target.value)}
                                        >
                                            <option value="" disabled>Select Behavior</option>
                                            {behaviorList.map(behavior => (
                                                <option key={behavior.id} value={behavior.name}>
                                                    {behavior.name}
                                                </option>
                                            ))}
                                        </Form.Select>
                                    </Form.Group>
                                    <div className="d-flex justify-content-center">
                                        <Button variant="success" className='justify-content-center w-25' style={{ background: '#008080' }} onClick={() => {
                                            if (!skillLevelNumber || !skillLevelName || !skillLevelDescription || !skillLevelBehavior) {
                                                document.getElementById('alertmsg').innerHTML = 'Please fill all the fields';
                                                document.getElementById('alert').classList.remove('d-none');
                                                setTimeout(() => {
                                                    if (document.getElementById('alert')) {
                                                        document.getElementById('alert').classList.add('d-none');
                                                    }
                                                }, 3000);
                                                return;
                                            }
                                            else if (skillLevelNumber < 1) {
                                                document.getElementById('alertmsg').innerHTML = 'Level Number must be greater than 0';
                                                document.getElementById('alert').classList.remove('d-none');
                                                setTimeout(() => {
                                                    if (document.getElementById('alert')) {
                                                        document.getElementById('alert').classList.add('d-none');
                                                    }
                                                }
                                                , 3000);
                                                return;
                                            }
                                            handleFormSubmit();
                                        }}>
                                            {modalData ? 'Update' : 'Add'}
                                        </Button>
                                    </div>
                                </Form>
                            </Modal.Body>
                        </Modal>
                    </Container>
                </>
            )}
        </div>
    );
}

export default SkillLevel;