import axios from 'axios';
import apiClient from './Auth';

export const getCriteria = async () => {
    const response = await apiClient.get('/main/get_criteria');
    return response.data;
}

export const addCriteria = async (criteria) => {
    const response = await apiClient.post('/main/create_criteria/', criteria);
    return response.data;
}

export const updateCriteria = async (id, criteria) => {
    const response = await apiClient.put(`/main/update_criteria/${id}/`, criteria);
    return response.data;
}

export const deleteCriteria = async (id) => {
    const response = await apiClient.delete(`/main/delete_criteria/${id}/`);
    return response.data;
}






    