import axios from 'axios';
import {jwtDecode} from 'jwt-decode'; 
import Swal from 'sweetalert2';

const BASE_URL = 'http://127.0.0.1:8000/api';
const apiClient = axios.create({
  baseURL: BASE_URL,
});

apiClient.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('access_token');
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

apiClient.interceptors.response.use(
    (response) => {
      return response;
    },
    async (error) => {
      const originalRequest = error.config;
      if (error.response && error.response.status === 401 && !originalRequest._retry) {
        originalRequest._retry = true;
        const refreshToken = localStorage.getItem('refresh_token');
        
        if (refreshToken) {
          try {
            const response = await axios.post(`${BASE_URL}/auth/token/refresh/`, { refresh: refreshToken });
            const newAccessToken = response.data.access;
            const newRefreshToken = response.data.refresh;
            localStorage.setItem('access_token', newAccessToken);
            localStorage.setItem('refresh_token', newRefreshToken);
            originalRequest.headers['Authorization'] = `Bearer ${newAccessToken}`;
            console.log(newRefreshToken);
            return apiClient(originalRequest);
          } catch (err) {
            console.log('Failed to refresh token:', err);
            Swal.fire({
              title: 'Error',
              text: 'Session expired. Please log in again.',
              icon: 'error',
            });
            logout();
            window.location.href = '/login';
          }
        }
      }
      
      return Promise.reject(error);
    }
  );
  

export const getToken = async (credentials) => {
  const response = await axios.post(`${BASE_URL}/auth/token/`, credentials);
  return response.data;
};

export const refreshToken = async (refreshToken) => {
  const response = await apiClient.post('/auth/token/refresh/', { refresh: refreshToken });
  return response.data;
};

export const userinfo = (token) => {
  if (!token) {
    return null;
  }
  const decodedToken = jwtDecode(token);
  let data = {
    username: decodedToken.username,
    email: decodedToken.email,
    role: decodedToken.role,
    manager: decodedToken.manager,
    id: decodedToken.user_id,
    seniority_level: decodedToken.seniority_level,
  };
  return data;
};

export const logout = () => { 
    localStorage.removeItem('access_token');
    localStorage.removeItem('refresh_token');
};

export default apiClient;