import axios from 'axios';
import apiClient from './Auth';

export const getUsersSkills = async () => {
    const response = await apiClient.get('/main/get_user_skills/');
    return response.data;
}

export const addUserSkill = async (userSkill) => {
    const response = await apiClient.post('/main/create_user_skill/', userSkill);
    return response.data;
}

export const updateUserSkill = async (id, userSkill) => {
    const response = await apiClient.put(`/main/update_user_skill/${id}/`, userSkill);
    return response.data;
}

export const deleteUserSkill = async (id) => {
    const response = await apiClient.delete(`/main/delete_user_skill/${id}/`);
    return response.data;
}

export const getUserSkillsByUserId = async (user_id) => {
    const response = await apiClient.get(`/main/get_user_skills_by_user_id/${user_id}/`);
    return response.data;
}

