import axios from 'axios';
import apiClient from './Auth';

export const getUsers = async () => {
    const response = await apiClient.get('/main/get_users');
    return response.data;
}

export const addUser = async (user) => {
    const response = await apiClient.post('/main/create_user/', user);
    return response.data;
}

export const updateUser = async (id, user) => {
    const response = await apiClient.put(`/main/update_user/${id}/`, user);
    return response.data;
}


export const deleteUser = async (id) => {
    const response = await apiClient.delete(`/main/delete_user/${id}/`);
    return response.data;
}

export const changePassword = async (id, old_password, new_password) => {
    const response = await apiClient.put(`/main/password_change/`, { old_password, new_password });
    return response.data;
  };
  
export const getEmployeeForManager = async () => {
    const response = await apiClient.get('/main/get_employee_for_manager/');
    return response.data;
}

export const getAllEmployee = async () => {
    const response = await apiClient.get('/main/get_all_employee/');
    return response.data;
}

export const getManagers = async () => {
    const response = await apiClient.get('/main/get_managers/');
    return response.data;
}

export const getUserById = async (id) => {
    const response = await apiClient.get(`/main/get_user_by_id/${id}/`);
    return response.data;
}
