
import axios from 'axios';
import apiClient from './Auth';

export const getElements = async () => {
    const response = await apiClient.get('/main/get_elements');
    return response.data;
}

export const addElement = async (element) => {  
    const response = await apiClient.post('/main/create_element/', element);
    return response.data;
}

export const updateElement = async (id, element) => {
    const response = await apiClient.put(`/main/update_element/${id}/`, element);
    return response.data;
}

export const deleteElement = async (id) => {
    const response = await apiClient.delete(`/main/delete_element/${id}/`);
    return response.data;
}

