import React, { useState, useEffect, useMemo } from 'react';
import Navbar from '../../components/Navbar';
import { Container, Row, Col, Button, Table, Pagination, Form, Modal, Spinner } from 'react-bootstrap';
import Swal from 'sweetalert2';
import { getEmployeeForManager } from '../../Api/User';
function EmployeesSkills(props) {
    const [userList, setUserList] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 8;
    const [filter, setFilter] = useState({ field: '', query: '', orderBy: '' });
    const [loading, setLoading] = useState(false);
    useEffect(() => {
        document.title = `Employee for ${props.user.username} - Siemens Employee Management System`;
        const fetchData = async () => {
            setLoading(true);
            try {
                const data = await getEmployeeForManager();
                setUserList(data);
            } catch (error) {
                Error('An error occurred while fetching data');
            } finally {
                setLoading(false);
            }
        };
        fetchData();
    }, []);
    const filteredUsers = useMemo(() => {
        const { field, query } = filter;
        if (!field) return userList;

        return userList.filter(user => {
            let value = user[field];
            if (field === 'seniority_level') {
                value = parseInt(value);
            }
            if (value === undefined || value === null) return false;
            return value.toString().toLowerCase().includes(query.toLowerCase());
        });
    }, [userList, filter]);

    const sortedUsers = useMemo(() => {
        if (!filter.orderBy) return filteredUsers;

        return [...filteredUsers].sort((a, b) => {
            const field = filter.orderBy;
            let valA = a[field];
            let valB = b[field];
             if (field === 'seniority_level') { 
                valA = parseInt(valA);
                valB = parseInt(valB);
        
            }


            if (typeof valA === 'string') {
                return valA.localeCompare(valB);
            } else if (typeof valA === 'number') {
                return valA - valB;
            }

            return 0;
        });
    }, [filteredUsers, filter.orderBy]);

    const currentUsers = useMemo(() => {
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        return sortedUsers.slice(indexOfFirstItem, indexOfLastItem);
    }, [sortedUsers, currentPage, itemsPerPage]);

    const totalPages = Math.ceil(sortedUsers.length / itemsPerPage);

    useEffect(() => {
        const newTotalPages = Math.ceil(sortedUsers.length / itemsPerPage);
        if (currentPage > newTotalPages) {
            setCurrentPage(newTotalPages > 0 ? newTotalPages : 1);
        }
    }, [sortedUsers.length, itemsPerPage, currentPage]);

    const handleSearch = (e) => {
        const { name, value } = e.target;
        setFilter(prev => ({ ...prev, [name]: value }));
        setCurrentPage(1);
    };

    return (
        <div id="pages" className="m-0 p-0">
            {loading ? (
                <div className="d-flex justify-content-center align-items-center bg-light" style={{ height: '100vh' }}>
                    <Spinner animation="border" role="status" style={{ width: '50px', height: '50px', color: '#008080' }}>
                        <span className="visually-hidden">Loading...</span>
                    </Spinner>
                </div>
            ) : (
                <>
                    <Navbar user={props.user} className="mb-5" />
                    <Container className="mt-5">
                        <Row className="mb-3 align-items-center justify-content-center">
                            <Col lg={3} className="text-center mb-2">
                                <h3>Users List</h3>
                            </Col>

                            <Col lg={4} className="mb-2">
                                <div className="d-flex border border-2 rounded-3">
                                    <Form.Select
                                        className="border-end"
                                        style={{ width: '40%' }}
                                        name="field"
                                        value={filter.field}
                                        onChange={handleSearch}
                                        aria-label="Filter by"
                                    >
                                        <option value="" disabled>Filter by</option>
                                        <option value="username">Username</option>
                                        <option value="email">Email</option>
                                        <option value="role">Role</option>
                                        <option value="seniority_level">Seniority Level</option>
                                        <option value="manager_id">Manager</option>
                                    </Form.Select>

                                    <div style={{ display: 'flex', alignItems: 'center', width: '60%' }} id="search">
                                        <Form.Control
                                            type="text"
                                            name="query"
                                            value={filter.query}
                                            onChange={handleSearch}
                                            placeholder="Search"
                                            style={{ width: '100%' }}
                                            aria-label="Search"
                                        />
                                        <i
                                            className="fa fa-search"
                                            style={{
                                                color: '#6c757d',
                                                textAlign: 'end',
                                                marginLeft: '-30px',
                                            }}
                                            aria-hidden="true"
                                        ></i>
                                    </div>
                                </div>
                            </Col>

                            <Col lg={2} className="mb-2 d-flex">
                                <Form.Select
                                    className="me-2"
                                    value={filter.orderBy}
                                    onChange={(e) => { setFilter(prev => ({ ...prev, orderBy: e.target.value })); setCurrentPage(1); }}
                                    aria-label="Order by"
                                >
                                    <option value="" disabled>Order by</option>
                                    <option value="username">Username</option>
                                    <option value="email">Email</option>
                                    <option value="role">Role</option>
                                    <option value="seniority_level">Seniority Level</option>
                                    <option value="manager_id">Manager</option>
                                </Form.Select>
                            </Col>

                
                        </Row>

                        <hr className="mt-3 text-muted" />

                        <div className="table-responsive">
                            <Table hover striped className="text-center mt-3">
                                <thead>
                                    <tr>
                                        <th>Username</th>
                                        <th>Email</th>
                                        <th>Role</th>
                                        <th>Seniority Level</th>
                                        <th>Skills</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {currentUsers.length > 0 ? (
                                        currentUsers.map(user => (
                                            console.log(user),  
                                            <tr key={user.id}>
                                                <td>{user.username}</td>
                                                <td>{user.email}</td>
                                                <td>{user.role}</td>
                                                <td>{user.seniority_level}</td>
                                                <td>
                                                <a href={`/skills/user-skill/${user.id}`}> <i className="fa fa-eye" aria-hidden="true"></i></a>
                                                </td>
                                            </tr>
                                        ))
                                    ) : (
                                        <tr>
                                            <td colSpan="5">No users found</td>
                                        </tr>
                                    )}
                                </tbody>
                            </Table>
                        </div>

                        {totalPages > 1 && (
                            <Pagination className="justify-content-center">
                                <Pagination.Prev
                                    onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                                    disabled={currentPage === 1}
                                >
                                    Previous
                                </Pagination.Prev>
                                {Array.from({ length: totalPages }, (_, index) => (
                                    <Pagination.Item
                                        key={index + 1}
                                        active={index + 1 === currentPage}
                                        onClick={() => setCurrentPage(index + 1)}
                                    >
                                        {index + 1}
                                    </Pagination.Item>
                                ))}
                                <Pagination.Next
                                    onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                                    disabled={currentPage === totalPages}
                                >
                                    Next
                                </Pagination.Next>
                            </Pagination>
                        )}
                    </Container>
                </>
            )}
        </div>
    );
}

export default EmployeesSkills;
