import React, { useState, useEffect, useMemo } from 'react';
import Navbar from '../../../components/Navbar';
import { Container, Row, Col, Button, Table, Pagination, Form, Modal } from 'react-bootstrap';
import Swal from 'sweetalert2';
import {getBehaviors,addBehavior,updateBehavior,deleteBehavior} from '../../../Api/Behavior';
import {getCriteria} from '../../../Api/Criteria';
import { Spinner } from 'react-bootstrap';
import Success from '../../../components/Sucees';
import Error from '../../../components/Erorr';
import Warning from '../../../components/Warning';

function Behavior(props) {
    const [behaviorList, setBehaviorList] = useState([]);
    const [criteriaList, setCriteriaList] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 8;
    const [filter, setFilter] = useState({ field: '', query: '', orderBy: '' });
    const [showModal, setShowModal] = useState(false);
    const [modalData, setModalData] = useState(null);
    
    const [behaviorName, setBehaviorName] = useState('');
    const [behaviorDefinition, setBehaviorDefinition] = useState('');
    const [behaviorDescription, setBehaviorDescription] = useState('');
    const [behaviorSeniorityLevel, setBehaviorSeniorityLevel] = useState('');
    const [behaviorCriteria, setBehaviorCriteria] = useState('');
    const [loading, setLoading] = useState(false);


    useEffect(() => {
        document.title ="Behaviors - Siemens Employee Management System";
        const fetchData = async () => {
            setLoading(true);
            try {
            const behaviorResponse = await getBehaviors();
            setBehaviorList(behaviorResponse);
            const criteriaResponse = await getCriteria();
            setCriteriaList(criteriaResponse);
        } 
        catch (error) {
            Error('An error occurred while fetching criteria');
        } 
        finally {
            setLoading(false);
        }
    };
    fetchData();
    }, []);  

    useEffect(() => {
        if (modalData) {
            setBehaviorName(modalData.name);
            setBehaviorDefinition(modalData.definition);
            setBehaviorDescription(modalData.description);
            setBehaviorSeniorityLevel(modalData.seniority_level);
            setBehaviorCriteria(modalData.criteria);
        } else {
            setBehaviorName('');
            setBehaviorDefinition('');
            setBehaviorDescription('');
            setBehaviorSeniorityLevel('');
            setBehaviorCriteria('');
        }
    }, [modalData]);


    const filteredBehaviors = useMemo(() => {
        const { field, query } = filter;
        if (!field) return behaviorList;

        return behaviorList.filter(behavior => {
            let value = behavior[field];
            if (field === 'created_at') {
                value = new Date(behavior[field]).toLocaleString(); 
            } else if (field === 'criteria_id') {
                value = behavior.criteria
            }
            if (value === undefined || value === null) return false;
            return value.toString().toLowerCase().includes(query.toLowerCase());
        });
    }, [behaviorList, filter, criteriaList]);

    const sortedBehaviors = useMemo(() => {
        if (!filter.orderBy) return filteredBehaviors;

        return [...filteredBehaviors].sort((a, b) => {
            const field = filter.orderBy;
            let valA = a[field];
            let valB = b[field];

            if (field === 'created_at') {
                valA = new Date(a[field]);
                valB = new Date(b[field]);
            } else if (field === 'criteria_id') {
                valA =  a.criteria;
                valB =  b.criteria;
            }

            if (typeof valA === 'string') {
                return valA.localeCompare(valB);
            } else if (typeof valA === 'number' || valA instanceof Date) {
                return valA - valB; 
            }

            return 0;
        });
    }, [filteredBehaviors, filter.orderBy, criteriaList]);

    const currentBehaviors = useMemo(() => {
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        return sortedBehaviors.slice(indexOfFirstItem, indexOfLastItem);
    }, [sortedBehaviors, currentPage, itemsPerPage]);

    const totalPages = Math.ceil(sortedBehaviors.length / itemsPerPage);

    useEffect(() => {
        const newTotalPages = Math.ceil(sortedBehaviors.length / itemsPerPage);
        if (currentPage > newTotalPages) {
            setCurrentPage(newTotalPages > 0 ? newTotalPages : 1);
        }
    }, [sortedBehaviors.length, itemsPerPage, currentPage]);

    const handleDelete = (id) => {
        if (!id) {
            Error('Criteria Not Found ! Please Refresh the Page');
            return;
        }

        Warning('Are you sure?', 'Do you want to delete this Behavior?', () => {
            deleteBehavior(id).then((response) => {
                const updatedList = behaviorList.filter(behavior => behavior.id !== id);
                setBehaviorList(updatedList);
                Success(response);
            }).catch((error) => {
                Error(error.response.data); 
            });
        });

    };
    const handleAdd = () => {
        setModalData(null);
        setShowModal(true);
    };

    const handleEdit = (behavior) => {
        setModalData(behavior);
        setShowModal(true);
    };

    const handleFormSubmit = () => {
        const formData = {
            name:  behaviorName,
            definition: behaviorDefinition,
            description: behaviorDescription,
            seniority_level: behaviorSeniorityLevel,
            created_at: new Date().toISOString(),
            criteria : criteriaList.find(criteria => criteria.name === behaviorCriteria).id
        };
        if (modalData) {
            if (behaviorList.findIndex(behavior =>  behavior.id !== modalData.id) === -1) {
                Error('Behavior Not Found ! Please Refresh the Page');
                return;
            }
            else if (behaviorList.find(behavior => behavior.name === formData.name && behavior.id !== modalData.id)) {
                Error('Behavior with this name already exists');
                return;
            }
            updateBehavior(modalData.id, formData).then((response) => {
                const updatedList = behaviorList.map(behavior => (behavior.id === modalData.id ?  response.new_data : behavior));
                setBehaviorList(updatedList);
                Success(response.message);
            }).catch((error) => {
            console.log(error);
                Error(error.response.data);
            });
            
        } else {
            if (behaviorList.find(behavior => behavior.name === formData.name)) {
                Error('Behavior with this name already exists');
                return;
            }
            addBehavior(formData).then((response) => {
                setBehaviorList([...behaviorList, response.new_data]);
                Success(response.message);
            }).catch((error) => {
                Error(error.response.data);
            });
        }
        setBehaviorName('');
        setBehaviorDefinition('');
        setBehaviorDescription('');
        setBehaviorSeniorityLevel('');
        setBehaviorCriteria('');
        setShowModal(false);
    };

    const handleSearch = (e) => {
        const { name, value } = e.target;
        setFilter(prev => ({ ...prev, [name]: value }));
        setCurrentPage(1);
    };

    return (
        <div id="pages" className="m-0 p-0">
              {loading ? (
                <div className="d-flex justify-content-center align-items-center bg-light" style={{ height: '100vh' }}>
                <Spinner animation="border" role="status" style={{ width: '50px', height: '50px', color: '#008080' }}>
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
                </div>
            ) : (
            <>
            <Navbar user={props.user} className="mb-5" />
            <Container className="mt-5">
                <Row className="mb-3 align-items-center">
                    {/* Heading */}
                    <Col lg={3} className="text-center mb-2">
                        <h3>Behaviors List</h3>
                    </Col>

                    <Col lg={4} className="mb-2">
                        <div className="d-flex border border-2 rounded-3">
                            <Form.Select
                                className="border-end"
                                style={{ width: '40%' }}
                                name="field"
                                value={filter.field}
                                onChange={handleSearch}
                                aria-label="Filter by"
                            >
                                <option value="" disabled> Filter by</option>
                                <option value="name">Name</option>
                                <option value="definition">Definition</option>
                                <option value="description">Description</option>
                                <option value="seniority_level">Seniority Level</option>
                                <option value="created_at">Created At</option>
                                <option value="criteria_id">Criteria</option>
                            </Form.Select>

                            <div style={{ display: 'flex', alignItems: 'center', width: '60%' }} id="search">
                                <Form.Control
                                    type="text"
                                    name="query"
                                    value={filter.query}
                                    onChange={handleSearch}
                                    placeholder="Search"
                                    style={{ width: '100%' }}
                                    aria-label="Search"
                                />
                                <i
                                    className="fa fa-search"
                                    style={{
                                        color: '#6c757d',
                                        textAlign: 'end',
                                        marginLeft: '-30px',
                                    }}
                                    aria-hidden="true"
                                ></i>
                            </div>
                        </div>
                    </Col>

                    <Col lg={2} className="mb-2 d-flex">
                        <Form.Select
                            className="me-2"
                            value={filter.orderBy}
                            onChange={(e) => { setFilter(prev => ({ ...prev, orderBy: e.target.value })); setCurrentPage(1); 
                        }}
                        aria-label="Order by"
                    >
                        <option value="" disabled>Order by</option>
                        <option value="name">Name</option>
                        <option value="definition">Definition</option>
                        <option value="description">Description</option>
                        <option value="seniority_level">Seniority Level</option>
                        <option value="created_at">Created At</option>
                        <option value="criteria_id">Criteria</option>
                    </Form.Select>
                </Col>

                <Col lg={3} className="text-center">
                    <Button variant="primary" onClick={handleAdd}>
                        <i className="bi bi-plus me-2"></i> Add New Behavior
                    </Button>
                </Col>
            </Row>

            <hr className="mt-3 text-muted" />

            <Table hover striped className="text-center mt-3" >
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Definition</th>
                        <th>Description</th>
                        <th>Seniority Level</th>
                        <th>Created At</th>
                        <th>Criteria</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {currentBehaviors.length > 0 ? (
                        currentBehaviors.map(behavior => (
                            <tr key={behavior.id}>
                                <td>{behavior.name}</td>
                                <td>{behavior.definition}</td>
                                <td>{behavior.description}</td>
                                <td>{behavior.seniority_level}</td>
                                <td>{new Date(behavior.created_at).toLocaleString()}</td>
                                <td>{behavior.criteria}</td>
                                <td>
                                    <i
                                        className="fa fa-pencil me-3"
                                        style={{ cursor: 'pointer' }}
                                        onClick={() => handleEdit(behavior)}
                                        aria-hidden="true"
                                    ></i>
                                    <i
                                        className="fa fa-trash"
                                        style={{ cursor: 'pointer' }}
                                        onClick={() => handleDelete(behavior.id)}
                                        aria-hidden="true"
                                    ></i>
                                </td>
                            </tr>
                        ))
                    ) : (
                        <tr>
                            <td colSpan="7">No behaviors found</td>
                        </tr>
                    )}
                </tbody>
            </Table>

            {totalPages > 1 && (
                <Pagination className="justify-content-center">
                    <Pagination.Prev
                        onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                        disabled={currentPage === 1}
                    >
                        Previous
                    </Pagination.Prev>
                    {Array.from({ length: totalPages }, (_, index) => (
                        <Pagination.Item
                            key={index + 1}
                            active={index + 1 === currentPage}
                            onClick={() => setCurrentPage(index + 1)}
                        >
                            {index + 1}
                        </Pagination.Item>
                    ))}
                    <Pagination.Next
                        onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                        disabled={currentPage === totalPages}
                    >
                        Next
                    </Pagination.Next>
                </Pagination>
            )}

            <Modal show={showModal} onHide={() => setShowModal(false)} backdrop="static" keyboard={false} centered>
                <Modal.Header closeButton>
                    <Modal.Title className='text-center w-100'>{modalData ? 'Edit Behavior' : 'Add New Behavior'}</Modal.Title>
                </Modal.Header>
                <Modal.Body>  
                    <Form>
                        <Form.Group className="mb-3 d-none" controlId="alert" id='alert'>
                            <Form.Text >
                            <div className="alert alert-danger text-center" role="alert" id='alert_message'>
                            </div>
                            </Form.Text>
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="behaviorName">
                            <Form.Label>Name</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder="Enter name of the behavior"
                                value={behaviorName}
                                onChange={(e) => {
                                    setBehaviorName(e.target.value);
                                }}

                            />                            
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="behaviorDefinition">
                            <Form.Label>Definition</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder="Enter definition of the behavior"
                                value={behaviorDefinition}
                                onChange={(e) => {
                                    setBehaviorDefinition(e.target.value);
                                }}
                            />                            
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="behaviorDescription">
                            <Form.Label>Description</Form.Label>
                            <Form.Control
                                as="textarea"
                                placeholder="Enter description"
                                value={behaviorDescription}
                                onChange={(e) => {
                                    setBehaviorDescription(e.target.value);
                                }}
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="behaviorSeniorityLevel">
                            <Form.Label>Seniority Level</Form.Label>
                            <Form.Control
                                type="number"
                                placeholder="Enter seniority level"
                                value={behaviorSeniorityLevel}
                                onChange={(e) => {
                                    setBehaviorSeniorityLevel(e.target.value);
                                }}
                                min={1}
                                max={4}

                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="behaviorCriteria">
                            <Form.Label>Criteria</Form.Label>
                            <Form.Select
                                aria-label="Select Criteria"
                                id="behaviorCriteria"
                                value={behaviorCriteria}
                                onChange={(e) => {
                                    setBehaviorCriteria(e.target.value);
                                }}
                            >
                                <option value="" disabled>Select Criteria</option>
                                {criteriaList.map(criteria => (
                                    <option key={criteria.id} value={criteria.name}>
                                        {criteria.name}
                                    </option>
                                ))}
                            </Form.Select>
                        </Form.Group>
                        <div className="d-flex justify-content-center">
                        <Button variant="success" className='justify-content-center w-25' style={{ background: '#008080' }} onClick={() => {
                            if (!behaviorName || !behaviorDefinition || !behaviorDescription || !behaviorSeniorityLevel || !behaviorCriteria) {
                                document.getElementById('alert_message').innerHTML = 'All fields are required';
                                document.getElementById('alert').classList.remove('d-none');
                                setTimeout(() => {
                                    if (document.getElementById('alert')) {
                                        document.getElementById('alert').classList.add('d-none');
                                    }
                                }, 3000);
                                return;
                            }
                            else if (behaviorSeniorityLevel >4 || behaviorSeniorityLevel <1) {
                                document.getElementById('alert_message').innerHTML = 'Seniority Level must be between 1 and 4';
                                document.getElementById('alert').classList.remove('d-none');
                                setTimeout(() => {
                                    if (document.getElementById('alert')) {
                                        document.getElementById('alert').classList.add('d-none');
                                    }
                                }, 3000);
                                return;
                            }
                            handleFormSubmit();
                        }}>
                            {modalData ? 'Update' : 'Add'}
                        </Button>
                        </div>
                    </Form>
                </Modal.Body>
            </Modal>
        </Container>
    </> 
    )}
    </div>
);
}

export default Behavior;
