
import axios from 'axios';
import apiClient from './Auth';

export const getBehaviors = async () => {
    const response = await apiClient.get('/main/get_behaviors/');
    return response.data;
}

export const addBehavior = async (behavior) => {
    const response = await apiClient.post('/main/create_behavior/', behavior);
    return response.data;
}

export const updateBehavior = async (id, behavior) => {
    const response = await apiClient.put(`/main/update_behavior/${id}/`, behavior);
    return response.data;
}

export const deleteBehavior = async (id) => {
    const response = await apiClient.delete(`/main/delete_behavior/${id}/`);
    return response.data;
}

