import Swal from 'sweetalert2';

const Warning = ( title, text, onConfirm ) => {
    Swal.fire({
        title: title,
        text: text,
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#008080',
        iconColor: '#008080',
        confirmButtonText: 'Yes',
    }).then((result) => {
        if (result.isConfirmed) {
        onConfirm();
        }
    });
    
}

export default Warning;

