import React, { useState, useEffect, useMemo } from 'react';
import Navbar from '../../../components/Navbar';
import { Container, Row, Col, Button, Table, Pagination, Form, Modal } from 'react-bootstrap';
import { getElements, addElement, updateElement, deleteElement } from '../../../Api/Element';
import Success from '../../../components/Sucees';
import Error from '../../../components/Erorr';
import Warning from '../../../components/Warning';
import Swal from 'sweetalert2';
import { Spinner } from 'react-bootstrap';

function Element(props) {
    const [elementList, setElementList] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 8;
    const [filter, setFilter] = useState({ field: '', query: '', orderBy: '' });
    const [showModal, setShowModal] = useState(false);
    const [modalData, setModalData] = useState(null);

    const [elementName, setElementName] = useState('');

    const [loading, setLoading] = useState(false);


    useEffect(() => {
        document.title ="Elements - Siemens Employee Management System";
        const fetchData = async () => {
            setLoading(true);
            try {
            const response =await getElements()
                setElementList(response);
            }
            catch (error) {
                Error("An error occurred while fetching data");
            } 
            finally {
                setLoading(false);
            };        
        };
        fetchData();
    }, []);

    useEffect(() => {
        if (modalData) {
            setElementName(modalData.name);
        } else {
            setElementName('');
        }
    }, [modalData]);

    const filteredElements = useMemo(() => {
        const { field, query } = filter;
        if (!field) return elementList;

        return elementList.filter((element) => {
            let value = element[field];
            if (field === 'created_at') {
                value = new Date(element[field]).toLocaleString();
            }
            if (value === undefined || value === null) return false;
            return value.toString().toLowerCase().includes(query.toLowerCase());
        });
    }, [elementList, filter]);

    const sortedElements = useMemo(() => {
        if (!filter.orderBy) return filteredElements;

        return [...filteredElements].sort((a, b) => {
            const field = filter.orderBy;
            let valA = a[field];
            let valB = b[field];

            if (field === 'created_at') {
                valA = new Date(a[field]);
                valB = new Date(b[field]);
            }

            if (typeof valA === 'string') {
                return valA.localeCompare(valB);
            } else if (typeof valA === 'number' || valA instanceof Date) {
                return valA - valB;
            }

            return 0;
        });
    }, [filteredElements, filter.orderBy]);

    const currentElements = useMemo(() => {
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        return sortedElements.slice(indexOfFirstItem, indexOfLastItem);
    }, [sortedElements, currentPage, itemsPerPage]);

    const totalPages = Math.ceil(sortedElements.length / itemsPerPage);

    useEffect(() => {
        const newTotalPages = Math.ceil(sortedElements.length / itemsPerPage);
        if (currentPage > newTotalPages) {
            setCurrentPage(newTotalPages > 0 ? newTotalPages : 1);
        }
    }, [sortedElements.length, itemsPerPage, currentPage]);

    const handleDelete = (id) => {
        if (!id) {
            Error('Element Not Found ! Please Refresh the Page');
            return;
        }       
        Warning("Warning", "Are you sure you want to delete this element?", () => {
            deleteElement(id)
            .then((response) => {
                setElementList(elementList.filter((e) => e.id !== id));
                Success(response);
            })
            .catch((error) => {
                Error(error.response.data);
            });
        });
    };

    const handleAdd = () => {
        setModalData(null);
        setShowModal(true);
    };

    const handleEdit = (element) => {
        setModalData(element);
        setShowModal(true);
    };

    const handleFormSubmit = () => {
        const formData = { name: elementName, created_at: new Date().toISOString()};
        if (modalData) {
            if (elementList.findIndex((e) => e.id === modalData.id) === -1) {
                Error('Element Not Found ! Please Refresh the Page');
                return;
            }
            else if (elementList.find((e) => e.name === elementName && e.id !== modalData.id)) {
                Error('Element with the same name already exists');
                return;
            }
            formData.created_at = elementList.find((e) => e.id === modalData.id).created_at;
            updateElement(modalData.id, formData)
                .then((response) => {
                    let newdata = response.new_data; 
                    setElementList(elementList.map((e) => (e.id === newdata.id ? newdata : e)));
                    Success(response.message);
                })
                .catch((error) => {
                    Error(error.response.data);
                    window.location.reload();
                });
        } else {
            if (elementList.find((e) => e.name === elementName)) {
                Error('Element with the same name already exists');
                return;
            }
            addElement(formData)
                .then((response) => {
                    Success(response.message);
                    let newdata = response.new_data; 
                    setElementList([...elementList, newdata]);
                })
                .catch((error) => {
                    Error(error.response.data);
                });
        }
        setElementName('');
        setShowModal(false); 
    };

    const handleSearch = (e) => {
        const { name, value } = e.target;
        setFilter((prev) => ({ ...prev, [name]: value }));
        setCurrentPage(1);
    };

    return (
        <div id="pages" className="m-0 p-0">
             {loading ? (
                <div className="d-flex justify-content-center align-items-center bg-light" style={{ height: '100vh' }}>
                <Spinner animation="border" role="status" style={{ width: '50px', height: '50px', color: '#008080' }}>
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
                </div>
            ) : (
            <>
            <Navbar user={props.user} className="mb-5" />
            <Container className="mt-5">
                <Row className="mb-3 align-items-center">
                    <Col lg={3} className="text-center mb-2">
                        <h3>Elements List</h3>
                    </Col>

                    <Col lg={4} className="mb-2">
                        <div className="d-flex border border-2 rounded-3">
                            <Form.Select
                                className="border-end"
                                style={{ width: '40%' }}
                                name="field"
                                value={filter.field}
                                onChange={handleSearch}
                                aria-label="Filter by"
                            >
                                <option value="" disabled>
                                    {' '}
                                    Filter by
                                </option>
                                <option value="name">Name</option>
                                <option value="created_at">Created At</option>
                            </Form.Select>

                            <div style={{ display: 'flex', alignItems: 'center', width: '60%' }} id="search">
                                <Form.Control
                                    type="text"
                                    name="query"
                                    value={filter.query}
                                    onChange={handleSearch}
                                    placeholder="Search"
                                    style={{ width: '100%' }}
                                    aria-label="Search"
                                />
                                <i
                                    className="fa fa-search"
                                    style={{
                                        color: '#6c757d',
                                        textAlign: 'end',
                                        marginLeft: '-30px',
                                    }}
                                    aria-hidden="true"
                                ></i>
                            </div>
                        </div>
                    </Col>

                    <Col lg={2} className="mb-2 d-flex">
                        <Form.Select
                            className="me-2"
                            value={filter.orderBy}
                            onChange={(e) => {
                                setFilter((prev) => ({ ...prev, orderBy: e.target.value }));
                                setCurrentPage(1);
                            }}
                            aria-label="Order by"
                        >
                            <option value="" disabled>
                                Order by
                            </option>
                            <option value="name">Name</option>
                            <option value="created_at">Created At</option>
                        </Form.Select>
                    </Col>

                    <Col lg={3} className="text-center">
                        <Button variant="primary" onClick={handleAdd}>
                            <i className="bi bi-plus me-2"></i> Add New Element
                        </Button>
                    </Col>
                </Row>

                <hr className="mt-3 text-muted" />

                <Table hover striped className="text-center mt-3">
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Created At</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {currentElements.length > 0 ? (
                            currentElements.map((element) => (
                                <tr key={element.id}>
                                    <td>{element.name}</td>
                                    <td>{new Date(element.created_at).toLocaleString()}</td>
                                    <td>
                                        <i
                                            className="fa fa-pencil me-3"
                                            style={{ cursor: 'pointer' }}
                                            onClick={() => handleEdit(element)}
                                            aria-hidden="true"
                                        ></i>
                                        <i
                                            className="fa fa-trash"
                                            style={{ cursor: 'pointer' }}
                                            onClick={() => handleDelete(element.id)}
                                            aria-hidden="true"
                                        ></i>
                                    </td>
                                </tr>
                            ))
                        ) : (
                            <tr>
                                <td colSpan="3">No elements found</td>
                            </tr>
                        )}
                    </tbody>
                </Table>

                {totalPages > 1 && (
                    <Pagination className="justify-content-center">
                        <Pagination.Prev
                            onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
                            disabled={currentPage === 1}
                        >
                            Previous
                        </Pagination.Prev>
                        {Array.from({ length: totalPages }, (_, index) => (
                            <Pagination.Item
                                key={index + 1}
                                active={index + 1 === currentPage}
                                onClick={() => setCurrentPage(index + 1)}
                            >
                                {index + 1}
                            </Pagination.Item>
                        ))}
                        <Pagination.Next
                            onClick={() => setCurrentPage((prev) => Math.min(prev + 1, totalPages))}
                            disabled={currentPage === totalPages}
                        >
                            Next
                        </Pagination.Next>
                    </Pagination>
                )}

                <Modal show={showModal} onHide={() => setShowModal(false)} backdrop="static" keyboard={false} centered>
                    <Modal.Header closeButton>
                        <Modal.Title className="text-center w-100">{modalData ? 'Edit Element' : 'Add New Element'}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <Form.Group className="mb-3 d-none" controlId="alert" id="alert">
                                <Form.Text>
                                    <div className="alert alert-danger text-center" role="alert">
                                        Please fill out all the fields!
                                    </div>
                                </Form.Text>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="elementName">
                                <Form.Label>Name</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Enter name of the element"
                                    value= {elementName}
                                    onChange={(e) => setElementName(e.target.value)}
                                />
                            </Form.Group>
                            <div className="d-flex justify-content-center">
                                <Button
                                    variant="success"
                                    className="justify-content-center w-25"
                                    style={{ background: '#008080' }}
                                    onClick={() => {
                                        if (!elementName) {
                                            document.getElementById('alert').classList.remove('d-none');
                                            setTimeout(() => {
                                                if (document.getElementById('alert')) {
                                                    document.getElementById('alert').classList.add('d-none');
                                                }
                                            }, 3000);
                                            return;
                                        }
                                        handleFormSubmit();
                                    }}
                                >
                                    {modalData ? 'Update' : 'Add'}
                                </Button>
                            </div>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Container>
            </>
            )}
        </div>
    );
}

export default Element;

                                    