
import axios from 'axios';
import apiClient from './Auth';

export const getSkillLevels = async () => {
    const response = await apiClient.get('/main/get_skill_levels/');
    return response.data;
}

export const addSkillLevel = async (skillLevel) => {
    const response = await apiClient.post('/main/create_skill_level/', skillLevel);
    return response.data;
}

export const updateSkillLevel = async (id, skillLevel) => {
    const response = await apiClient.put(`/main/update_skill_level/${id}/`, skillLevel);
    return response.data;
}

export const deleteSkillLevel = async (id) => {
    const response = await apiClient.delete(`/main/delete_skill_level/${id}/`);
    return response.data;
}
